import Vue from "vue";
import VueRouter from "vue-router";
import QnA from "../views/QnA.vue";

import QnAList from "../components/QnAList.vue";
import QnARegist from "../components/QnARegist.vue";
import QnASearch from "../components/QnASearch.vue";
import QnAUpdate from "../components/QnAUpdate.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/mvqna",
    name: "QnA",
    component: QnA,
    redirect: "/mvqna/qnalist",
    children: [
      {
        path: "qnalist",
        name: "QnAList",
        component: QnAList,
      },
      {
        path: "qnasearch",
        name: "QnASearch",
        component: QnASearch,
      },
      {
        path: "qnaregist",
        name: "QnARegist",
        component: QnARegist,
      },
      {
        path: "qnaupdate",
        name: "QnAUpdate",
        component: QnAUpdate,
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
